.floor-two {
  height: 1080px;
  width: 100%;
  display: flex;
}
.floor-two .main-left {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.floor-two .main-left .canvas {
  position: relative;
  box-sizing: border-box;
  font-size: 0;
  width: 1600px;
}
.floor-two .main-left .canvas .detail {
  position: absolute;
  height: 300px;
  width: 560px;
  left: 50px;
  bottom: 0;
  padding: 10px;
  color: #fff;
  font-size: 18px;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.floor-two .main-left .canvas .detail h3 {
  font-size: 23px;
  color: #fff;
}
.floor-two .main-left .canvas .detail .window-information {
  display: flex;
  width: 70%;
  line-height: 30px;
}
.floor-two .main-left .canvas .detail .window-information .left {
  flex-grow: 1;
}
.floor-two .main-left .canvas .detail .window-information .right {
  flex-grow: 1;
}
.floor-two .main-left .canvas .detail .window-information .right .btn {
  background-color: #5084ff;
  width: 110px;
  height: 35px;
  line-height: 35px;
  text-align: center;
  border-radius: 7px;
  font-size: 16px;
  margin: 0 auto;
  cursor: pointer;
}
.floor-two .main-left .canvas .detail .window-information .right .btn > span {
  padding: 0 3px;
}
.floor-two .main-left .canvas .detail .window-information .right .btn:active {
  background-color: red;
}
.floor-two .main-left .canvas .detail .window-item {
  line-height: 30px;
}
.floor-two .main-left .canvas .detail .window-item h4 {
  color: #fff;
}
.floor-two .main-left .canvas .tips {
  position: absolute;
  width: 50%;
  height: 50px;
  line-height: 50px;
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 7px;
  bottom: 70px;
  left: 0;
  right: 0;
  margin: auto;
}
.floor-two .main-left .footer {
  height: 150px;
  margin: 0 60px;
  border-top: 1px dotted #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
  font-weight: 500;
  font-size: 30px;
}
.floor-two .main-left .footer .left-floor-num {
  display: flex;
  align-items: center;
  height: 100px;
}
.floor-two .main-left .footer .left-floor-num li {
  width: 100px;
  height: 50px;
  line-height: 50px;
  box-sizing: border-box;
  text-align: center;
  border: 1px solid #fff;
  border-radius: 10px;
  margin: 0 10px;
  cursor: pointer;
}
.floor-two .main-left .footer .left-floor-num li.active {
  background-color: #3767DD;
  border: none;
}
.floor-two .main-left .footer .fast-nav {
  display: flex;
  font-size: 30px;
}
.floor-two .main-left .footer .fast-nav > div {
  margin: 0 10px;
  padding: 10px;
  cursor: pointer;
  border-radius: 10px;
  border: 1px solid #fff;
}
.floor-two .main-left .footer .fast-nav > div i,
.floor-two .main-left .footer .fast-nav > div span {
  vertical-align: middle;
}
.floor-two .main-left .footer .fast-nav > div i {
  margin-right: 7px;
}
.floor-two .main-left .footer .fast-nav > div.active {
  background-color: #F4B710;
}
.floor-two .right-nav {
  box-sizing: border-box;
  width: 320px;
  padding: 50px 0;
  background-color: #454545;
  color: #fff;
}
.floor-two .right-nav .info {
  padding: 0 40px;
}
.floor-two .right-nav h3 {
  font-size: 30px;
  padding: 0 40px;
  color: #fff;
  margin-top: 100px;
  margin-bottom: 0;
}
.floor-two .right-nav .nav-container {
  margin-top: 0;
}
.floor-two .right-nav .nav-container .wrap li {
  height: 70px;
  font-size: 20px;
  line-height: 70px;
  padding: 0 40px;
  cursor: pointer;
}
.floor-two .right-nav .nav-container .wrap li span {
  display: block;
  height: 70px;
  box-sizing: border-box;
  border-bottom: 1px solid #fff;
}
.floor-two .right-nav .nav-container .wrap li span.no-border {
  border-bottom: none;
}
.floor-two .right-nav .nav-container .wrap li.active {
  background-color: #fff;
  color: #000;
}
